export class Timeout {
  private timer: NodeJS.Timeout | null = null;

  constructor(private delay: number) {}

  set<T>(cb: () => Promise<T>): void {
    this.cancel();

    this.timer = setTimeout(async () => {
      await cb();
      this.cancel();
    }, this.delay);
  }

  cancel(): void {
    if (this.timer !== null) {
      clearTimeout(this.timer);

      if (typeof this.timer.unref === 'function') {
        this.timer.unref();
      }

      this.timer = null;
    }
  }
}
